.authenticated {
  &-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    background-color: black;

    * {
      outline: none;
    }
  }

  &--title {
    font: {
      family: Ubuntu;
      size: 30px;
      weight: bold;
    }
    color: white;
  }

  &--content {
    display: flex;
    flex-direction: column;
    font: {
      family: Ubuntu;
      size: 25px;
      weight: bold;
    };
    color: whitesmoke;
  }

  &--button {
    padding: 10px 40px;
    font: {
      family: Ubuntu;
      size: 25px;
      weight: bold;
    }
    color: #285fb7;
    border-radius: 40px;
    border: 1px solid whitesmoke;
    margin: 20px auto;
  }
}